import React, { useEffect, useRef, useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCog, faFileDownload, faSave, faPrint, faTimes } from '@fortawesome/free-solid-svg-icons';


import marked from "marked";
import hljs from "highlight.js";
import 'highlight.js/styles/github.css';
import DOMPurify from 'dompurify';
import mermaid from 'mermaid';

const Mermaid = React.memo((props) => {
  const elementRef = useRef();
  const [delay, setDelay] = useState(0);
  const [leady, setLeady] = useState(false);
  useEffect(() => {
    setLeady(false);
    setDelay(setTimeout(() => {
      setLeady(true);
    }, 150));

    return () => {
      clearTimeout(delay);
    }
  }, [props]);

  useEffect(() => {
    if(!leady) return;
    mermaid.initialize({
      startOnLoad: true,
      theme: "default",
      securityLevel: "loose",
      themeCSS: `
        g.classGroup rect {
          fill: #282a36;
          stroke: #6272a4;
        } 
        g.classGroup text {
          fill: #f8f8f2;
        }
        g.classGroup line {
          stroke: #f8f8f2;
          stroke-width: 0.5;
        }
        .classLabel .box {
          stroke: #21222c;
          stroke-width: 3;
          fill: #21222c;
          opacity: 1;
        }
        .classLabel .label {
          fill: #f1fa8c;
        }
        .relation {
          stroke: #ff79c6;
          stroke-width: 1;
        }
        #compositionStart, #compositionEnd {
          fill: #bd93f9;
          stroke: #bd93f9;
          stroke-width: 1;
        }
        #aggregationEnd, #aggregationStart {
          fill: #21222c;
          stroke: #50fa7b;
          stroke-width: 1;
        }
        #dependencyStart, #dependencyEnd {
          fill: #00bcd4;
          stroke: #00bcd4;
          stroke-width: 1;
        } 
        #extensionStart, #extensionEnd {
          fill: #f8f8f2;
          stroke: #f8f8f2;
          stroke-width: 1;
        }`,
      fontFamily: "Fira Code"
    });
    mermaid.contentLoaded();
  }, [leady]);
  return leady && <div ref={elementRef} className="mermaid">{ props.chart }</div>;
});

const renderer = {
  heading: node => {
    const Heading = `h${node.depth}`;
    return <Heading>{ node.tokens.map(node => parser(node)) }</Heading>
  },
  blockquote: node => <blockquote>{ node.tokens.map(node => parser(node)) }</blockquote>,
  text: node => node?.tokens?.map(node => parser(node)) || node.raw,
  html: node => <div dangerouslySetInnerHTML={{__html: DOMPurify.sanitize(node.text, { IN_PLACE: true})}}></div>,
  code: node => {
    if (node.lang === 'mermaid') {
      return <Mermaid chart={ node.text } />
    }
    return <pre class="code" data-lang={node.lang}><code dangerouslySetInnerHTML={{__html: hljs.highlightAuto(node.text, [node.lang]).value}}></code></pre>
  },
  hr: _ => <hr />,
  list: node => {
    if (node.ordered) return <ol>{ node.items.map(node => parser(node)) }</ol>
    return <ul>{ node.items.map(node => parser(node))}</ul>
  },
  list_item: node => <li>{ node.checked !== undefined && <input type="checkbox" checked={node.checked} readOnly /> }{ node.tokens.map(node => parser(node)) }</li>,//(node.tokens || node.items).map(node => parser(node))
  paragraph: node => <p>{ node.tokens.map(node =>parser(node)) }</p>,
  table: root => <table>
    <thead className="">
      <tr>
        {root.tokens.header.map(node => <th className="p-1 px-2">{ node.map(node => parser(node)) }</th>)}
      </tr>
    </thead>
    <tbody className="">
      {root.tokens.cells.map(node => <tr>{ node.map((node, i) => <td className="p-1 px-2" style={{ textAlign: root.align[i], minWidth: '8em' }}>{ node.map(node => parser(node)) }</td>) }</tr>)}
    </tbody>
  </table>,

  strong: node => <strong dangerouslySetInnerHTML={{__html: DOMPurify.sanitize(node.text, { IN_PLACE: true})}}></strong>,
  em: node => <em dangerouslySetInnerHTML={{__html: DOMPurify.sanitize(node.text, { IN_PLACE: true})}}></em>,
  codespan: node => <code dangerouslySetInnerHTML={{__html: DOMPurify.sanitize(node.text, { IN_PLACE: true})}}></code>,
  br: _ => <br />,
  del: node => <del dangerouslySetInnerHTML={{__html: DOMPurify.sanitize(node.text, { IN_PLACE: true})}}></del>,
  link: node => <a href={ /^\s*javascript:/.test(node.href) || node.href } title={ node.title } dangerouslySetInnerHTML={{__html: DOMPurify.sanitize(node.text, { IN_PLACE: true})}}></a>,
  image: node => <img src={ /^\s*javascript:/.test(node.href) || node.href } alt={ node.text } title={ node.title } style={{maxWidth: '35%'}}/>,
}

const walkTokens = (token) => {
  console.log('walk');
  console.log(token);
  if (token.type === 'heading') {
    token.depth += 1;
  }
};

marked.use({ walkTokens });

//const parser = node => renderer[node.type]?.(node);
const parser = node => {console.log(node); return renderer[node.type]?.(node)};

const Preview = (props) => {
  // let docTitle = '';

  // const walkTokens = (token) => {
  //   console.log(token);
  //   if (token.type === 'heading') {
  //     if (token.depth === 1) {
  //       docTitle = docTitle || token.text;
  //       console.log(docTitle);
  //     }
  //   }
  // };
  
  // marked.use({ walkTokens });
  return (
    <article ref={props.articleRef} id="markdown-article" className="p-8 bg-white mx-auto min-h-full" style={{ width: '48em' }}>
      { marked.lexer(props.markdown).map(v => parser(v)) }
    </article>
  );
};

export default Preview;

