import React, { useEffect, useRef, useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCog, faFileDownload, faSave, faPrint, faTimes, faQuestion } from '@fortawesome/free-solid-svg-icons';

import './App.css';

import AceEditor from "react-ace";

import Preview from "./Preview.js";

function useLocalStorage(key, initialValue) {
  const [storedValue, setStoredValue] = useState(() => {
    try {
      const item = window.localStorage.getItem(key);
      return item ? JSON.parse(item) : initialValue;
    } catch (error) {
      console.log(error);
      return initialValue;
    }
  });

  const setValue = value => {
    try {
      const valueToStore = value instanceof Function ? value(storedValue) : value;
      setStoredValue(valueToStore);
      window.localStorage.setItem(key, valueToStore);
    } catch (error) {
      console.log(error);
    }
  };

  return [storedValue, setValue];
}

const Modal = props => {
  const [shouldRender, setRender] = useState(false);

  useEffect(() => {
    if(props.show) setRender(true);
  }, [props.show]);

  return (props.show || shouldRender) && <div className={`w-screen h-screen absolute bg-coolGray-900 bg-opacity-70 z-30 flex ${props.show && shouldRender ? 'opacity-100' : 'opacity-0'} transition-opacity`} onTransitionEndCapture={() => setRender(props.show)} onClick={ () => props?.onClose?.() }>
    <div className="bg-white flex flex-col relative m-auto p-4" onClick={ e => e.stopPropagation() }>
      <div className="text-right">
        <button className="text-lg" onClick={ () => props?.onClose?.() }><FontAwesomeIcon icon={faTimes} /></button>
      </div>
      { props.children }
    </div>
  </div>
}

const EditorConfig = () => {
  const [fontSize, setFontSize] = useLocalStorage('fontSize', 18);
  const [tabSize, setTabSize] = useLocalStorage('tabSize', 2);
  const [theme, setTheme] = useLocalStorage('theme', 'light');
  return <div className="flex flex-col space-y-8 w-64 px-4 m-2">
    <div className="flex space-x-4 text-lg font-bold">
      <label for="font-size" className="whitespace-nowrap text-coolGray-700 w-40">font size</label><input type="number" id="font-size" value={fontSize} className="focus:ring-1 focus:outline-none border-b border-coolGray-200 ring-coolGray-500 text-coolGray-700 bg-transparent w-full text-right" onChange={ e => setFontSize(parseInt(e.target.value))} />
    </div>
    <div className="flex space-x-4 text-lg font-bold">
      <label for="tab-size" className="whitespace-nowrap text-coolGray-700 w-40">tab size</label><input type="number" id="tab-size" value={tabSize} className="focus:ring-1 focus:outline-none border-b border-coolGray-200 ring-coolGray-500 text-coolGray-700 bg-transparent w-full text-right" onChange={ e => setTabSize(parseInt(e.target.value))} />
    </div>
    <div className="flex space-x-4 text-lg font-bold text-coolGray-700">
      <span className="w-40">theme</span>
      <div className="flex space-x-1"><input type="radio" id="light-theme" name="theme" value="light" className="my-auto" onChange={e => setTheme(e.target.value)} checked={theme === 'light' || undefined} /><label for="light-theme">light</label></div>
      <div className="flex space-x-1"><input type="radio" id="dark-theme" name="theme" value="dark" className="my-auto" onChange={e => setTheme(e.target.value)} checked={theme === 'dark' || undefined} /><label for="dark-theme">dark</label></div>
    </div>
    <div className="flex space-x-4 justify-end">
    </div>
  </div>
}

const PageConfig = () => {
  return <div className="flex flex-col space-y-8 w-64 px-4 m-2">
    <div className="flex space-x-4 text-lg font-bold">
      <label for="font-size" className="whitespace-nowrap text-coolGray-700 w-40">width</label><input type="number" id="font-size" className="focus:ring-1 focus:outline-none border-b border-coolGray-200 ring-coolGray-500 text-coolGray-700 bg-transparent w-full text-right" />
    </div>
    <div className="flex space-x-4 text-lg font-bold">
      <label for="tab-size" className="whitespace-nowrap text-coolGray-700 w-40">height</label><input type="number" id="tab-size" className="focus:ring-1 focus:outline-none border-b border-coolGray-200 ring-coolGray-500 text-coolGray-700 bg-transparent w-full text-right" />
    </div>
    <div className="flex space-x-4 text-lg font-bold text-coolGray-700">
      <span className="w-40">theme</span>
      <div className="flex space-x-1"><input type="radio" id="light-theme" name="theme" value="light" className="my-auto" /><label for="light-theme">light</label></div>
      <div className="flex space-x-1"><input type="radio" id="dark-theme" name="theme" value="dark" className="my-auto" /><label for="dark-theme">dark</label></div>
    </div>
    <div className="flex space-x-4 justify-end">
      <button className="rounded border-blue-500 border py-1 px-2 text-blue-500 w-20 hover:border-blue-400 hover:text-blue-400 transition">OK</button>
    </div>
  </div>
}

const App = () => {
  const [markdown, setMarkdown] = useState('');
  const [title, setTitle] = useLocalStorage('title', '');
  const [editorConfig, setEditorConfig] = useState({});
  const [showEditorConfig, setShowEditorConfig] = useState(false);
  const [showPageConfig, setShowPageConfig] = useState(false);
  const ref = useRef();

  useEffect(() => {
    setEditorConfig({fontSize: window.localStorage.getItem('fontSize'), tabSize: window.localStorage.getItem('tabSize'), theme: window.localStorage.getItem('theme')});
  }, [showEditorConfig]);
  const downloadMarkdown = () => {
    return URL.createObjectURL(new Blob([markdown], { type: "text/plain" }));
  }

  const openNewWindow = () => {
    let doc = `<head>${document.head.innerHTML}</head><body><article style="padding:4em;margin:auto;max-width:52em;">${ref.current.innerHTML}</article></body>`;
    window.open(URL.createObjectURL(new Blob([doc], { type: "text/html" })), '__blank');
  }

  return (
    <div className="w-full h-full relative flex flex-col">
      <Modal show={showEditorConfig} onClose={ () => {setShowEditorConfig(!showEditorConfig)} } >
        <EditorConfig />
      </Modal>
      <Modal show={showPageConfig} onClose={ () => setShowPageConfig(!showPageConfig) } >
        <PageConfig />
      </Modal>
      <header className="flex py-2 px-4 h-12 absolute top-0 w-full bg-coolGray-100 z-30">
        <span className="text-coolGray-500 whitespace-nowrap text-sm my-auto">simple markdown editor</span>
        <div className="ml-4 w-1/3 my-auto bg-coolGray-300 border rounded-full overflow-hidden">
          <input type="text" placeholder="title" className="w-full h-8 text-base px-3 bg-coolGray-200 border-none focus:outline-none" onChange={ e => setTitle(e.target.value) }/>
        </div>
        <a className="ml-auto text-xs text-coolGray-700 my-auto shadow-none border-none" onClick={() => setShowEditorConfig(true)}>
          ヘルプ
        </a>
        <button className="ml-2 rounded-full w-6 h-6 text-sm my-auto shadow-none border-none" onClick={() => setShowEditorConfig(true)}>
          <FontAwesomeIcon icon={faCog} className="text-coolGray-700" />
        </button>
      </header>
      <main className="pt-12 h-full w-full bg-coolGray-100">
        <div className="flex flex-col-reverse lg:flex-row max-h-full h-full w-full">
          <section className="xl:p-4 w-full flex flex-col h-52 lg:h-full">
            <div className="border border-coolGray-200 lg:h-full h-52">
              <AceEditor
                rerender={showEditorConfig}
                placeholder=""
                mode="markdown"
                theme={editorConfig.theme === 'light' ? 'tomorrow' : 'monokai' }
                name="ace-editor"
                onChange={e => setMarkdown(e)}
                height="100%"
                width="100%"
                fontSize={ parseInt(editorConfig.fontSize) || 18 }
                showPrintMargin={true}
                showGutter={true}
                highlightActiveLine={true}
                value={markdown}
                setOptions={{
                  enableBasicAutocompletion: true,
                  enableLiveAutocompletion: true,
                  enableSnippets: false,
                  showLineNumbers: true,
                  tabSize: parseInt(editorConfig.tabSize) || 4,
                }} />
            </div>
          </section>
          <section className="xl:p-4 flex flex-col h-full min-h-0 lg:border-l border-solid border-0 border-b xl:border-none relative">
            <div className="xl:shadow-lg overflow-y-auto h-full">
              <Preview articleRef={ref} markdown={markdown} />
            </div>
            <button className="bottom-8 right-8 shadow-xl absolute bg-purple-600 rounded-full w-10 h-10 text-lg border-none" onClick={() => openNewWindow()}>
              <FontAwesomeIcon icon={faPrint} className="text-white" />
            </button>
          </section>
        </div>
      </main>
    </div>
  );
};

export default App;